<template>
    <div class="m-card m-login-card">
        <card-header :title="$t('account.common.login')">
            <template #right>
                <lang-select :lang="lang" />
            </template>
        </card-header>

        <div class="m-card-main" v-if="!success">
            <el-form
                ref="loginForm"
                :model="form"
                label-position="top"
                :rules="rules"
                size="large"
                class="m-card-form"
                hide-required-asterisk
            >
                <el-form-item prop="username">
                    <template #label>
                        <div class="m-card-form-label">
                            <span>{{ $t("account.username.name") }}</span>
                        </div>
                    </template>
                    <el-input v-model.trim="form.username" size="large"> </el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <template #label>
                        <div class="m-card-form-label">
                            <span>{{ $t("account.common.password") }}</span>
                        </div>
                    </template>
                    <el-input
                        v-model.trim="form.password"
                        @keydown.enter="onLogin"
                        type="password"
                        size="large"
                        show-password
                    >
                    </el-input>
                </el-form-item>
            </el-form>
            <el-alert class="u-alert" v-if="error" type="error" show-icon :title="error"></el-alert>
            <el-button class="u-btn u-submit" type="primary" @click="onLogin" :disabled="!canSubmit">{{
                $t("account.common.login")
            }}</el-button>
        </div>

        <div v-else class="m-card-main">
            <el-alert
                class="m-alert"
                :title="$t('account.common.loginSuccess')"
                type="success"
                :description="$t('account.common.loginSuccessDesc')"
                show-icon
                :closable="false"
            >
            </el-alert>
            <a class="u-skip el-button u-btn el-button--primary" :href="redirect">{{ redirect_button }}</a>
        </div>
    </div>
    <div class="m-footer">
        <div class="m-footer-skip">
            {{ $t("account.common.noAccount") }}
            <a class="u-link" :href="registerLink">{{ $t("account.common.registerNow") }} üëâ</a>
        </div>
    </div>
</template>

<script>
import { loginByUsername } from "@/service/username";
import User from "@iruxu/rx-common/utils/user";
export default {
    name: "UsernameLogin",
    props: {
        app: {
            type: String,
            default: "",
        },
    },
    data() {
        return {
            form: {
                username: "",
                password: "",
            },

            rules: {
                username: [
                    { required: true, message: this.$t("account.username.namePlaceholder"), trigger: "blur" },
                    { min: 3, max: 20, message: this.$t("account.username.nameError"), trigger: "blur" },
                ],
                password: [
                    { required: true, message: this.$t("account.common.passwordPlaceholder"), trigger: "blur" },
                    { min: 6, max: 30, message: this.$t("account.common.passwordError"), trigger: "blur" },
                ],
            },

            error: "",
            success: false,
            redirect: "",
            redirect_button: "Ë∑≥ËΩ¨",
            homepage: "/",
        };
    },
    computed: {
        canSubmit() {
            return this.form.username && this.form.password;
        },
        registerLink() {
            const path = this.$router.resolve({ name: "username-register", query: { app: this.app } });

            return path.href;
        },
        lang() {
            return User.getLocale();
        },
    },
    mounted() {
        // ÁîüÊàêÁâπÂæÅÁ†Å
        User.generateFingerprint();
        this.checkDirect();
    },
    methods: {
        onLogin() {
            this.error = "";
            this.$refs.loginForm.validate(async (valid) => {
                if (valid) {
                    loginByUsername(this.form, { app: this.app })
                        .then((res) => {
                            this.$message.success(this.$t("account.common.loginSuccess"));
                            this.success = true;

                            User.update(res.data.data).then(() => {
                                this.skip();
                            });
                        })
                        .catch((err) => {
                            this.success = false;
                            this.error = err.data?.msg || this.$t("account.username.loginFailed");
                        });
                }
            });
        },
        checkDirect() {
            let search = new URLSearchParams(document.location.search);
            let redirect = search.get("redirect");
            if (redirect) {
                this.redirect = redirect;
                this.redirect_button = this.$t("account.common.jump");
            } else {
                this.redirect = this.homepage;
                this.redirect_button = this.$t("account.common.backToHome");
            }
        },
        skip() {
            if (this.redirect) {
                setTimeout(() => {
                    location.href = decodeURIComponent(this.redirect);
                }, 1200);
            }
        },
    },
};
</script>
