import { $uc } from "@iruxu/rx-common/utils/api";

// ============ Áî®Êà∑ÂêçÁõ∏ÂÖ≥ ============
/**
 * Êü•ËØ¢Áî®Êà∑ÂêçÁß∞ÊòØÂê¶ÂèØÁî®
 * @param {String} username Áî®Êà∑Âêç
 * @returns {Promise}
 */
export async function checkUsername(username) {
    return $uc()
        .get("/api/uc/user/account/username/valid", {
            params: {
                username,
            },
        })
        .then((res) => res.data.data?.isExist);
}

